export default {
    prepareProucts({ commit, state }, currentLocale = 'uk_EN'){
        const prods = state.products.map((product) => {
            const lang = currentLocale.indexOf('_') >-1 ? `${currentLocale.split('_')[0]}/` : '';
            const locale = currentLocale.indexOf('_') >-1 ? currentLocale.split('_')[1] : currentLocale;
            return {
                ...product,
                productUrl: `${state.basePandoraUrl.replace(/\{0\}/, locale.toLowerCase()).replace(/\{1\}/, lang)}${product.sku}.html`,
            };
        });
        commit('updateProducts', prods);
    },
	closeOpenModal(context){
		context.commit('closeModal');
	},
	openClosedModal(context, payload){
		context.commit('openModal', payload);
	},
};



