import Vue from 'vue';
export default {
    methods: {
        getProductLink(sku){
            console.log('test');
        },
        // url // [country].pandora.net[language]
        getCountryLink(countryCode, state){
            const lang = countryCode.indexOf('_') >-1 ? `${countryCode.split('_')[0]}/` : '';
            const locale = countryCode.indexOf('_') >-1 ? countryCode.split('_')[1] : countryCode;
            return state.basePandoraCampaignUrl.replace(/\{0\}/, locale.toLowerCase()).replace(/\{1\}/, lang);
        },
    }
};



