<template>
	<div class="modal-overlay" @click="closeOpenModal" ref="overlay" :class="[imagesLoaded ? 'loaded' : '']">
		<div class="modal" @click.stop ref="modal">
			<div class="modal-closeButton" @click="closeOpenModal"></div>
			<picture v-if="modal.isCard">
				<source media="(max-width: 399px)" :srcset="modal.content.images.cardSmall">
				<source media="(min-width: 400px)" :srcset="modal.content.images.cardLarge">
				<img :src="modal.content.images.cardLarge" @load="loaded">
			</picture>
			<div class="modal-content">
				<div class="modal-price">{{ modal.content.price }}</div>
				<a :href="modal.content.link" class="modal-link button">{{ $t('general_shop_now') }}</a>
			</div>
		</div>
	</div>
</template>

<script>
	import { mapState, mapActions } from 'vuex';

	export default {
		name: 'Modal',
		computed: mapState([
			'modal',
		]),
		data(){
			return {
				imagesLoaded: false,
			};
		},
		mounted() {
			this.position = this.$store.state.position;
			console.log( this.imagesLoaded );
			this.$nextTick(this.setupModal);
		},
		methods: {
			...mapActions([
				'closeOpenModal',
			]),
			setupModal() {
			},
			loaded() {
				this.imagesLoaded = true;
				console.log( this.imagesLoaded );
			},
		},
	};
</script>

<style lang="scss" scoped>
	@import '../assets/global';

	.modal-overlay {
		display: flex;
		align-items: center;
		justify-content: center;
		position: fixed;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		background: rgba(0, 0, 0, 0);
		z-index: 9999;
		transition: all 1s ease;

		&.loaded {
			background: rgba(0, 0, 0, .4);
		}
	}

	.modal {
		background: white;
		width: 90%;
		max-width: rem(1500);
		display: flex;
		align-items: center;
		position: relative;
		transform: scale(.9);
		opacity: 0;
		transition: all .6s ease;

		img {
			width: 100%;
			display: block;
		}

		.loaded & {
			transform: scale(1);
			opacity: 1;
		}
	}

	.modal-closeButton {
		cursor: pointer;
		height: rem(60);
		width: rem(60);
		background: white;
		position: absolute;
		top: rem(30);
		right: rem(30);
		border-radius: 50%;
		box-shadow: rem(10) rem(10) rem(40) rgba(0, 0, 0, .1);

		&:before, &:after {
			display: block;
			content: '';
			height: rem(2);
			width: rem(34);
			background: black;
			position: absolute;
			left: 50%;
			top: 50%;
			border-radius: rem(2);
			transform: translate(-50%) rotate(45deg);
		}

		&:after {
			transform: translate(-50%) rotate(-45deg);
		}

		&:hover {
			&:before {
				animation: closeHoverRight .5s ease;
			}

			&:after {
				animation: closeHoverLeft .5s ease;
			}
		}

		@keyframes closeHoverRight {
			0% {
				transform: translate(-50%) rotate(45deg);
			}
			50% {
				transform: translate(-50%) rotate(30deg);
			}
			100% {
				transform: translate(-50%) rotate(45deg);
			}
		}
		@keyframes closeHoverLeft {
			0% {
				transform: translate(-50%) rotate(-45deg);
			}
			50% {
				transform: translate(-50%) rotate(-30deg);
			}
			100% {
				transform: translate(-50%) rotate(-45deg);
			}
		}

	}

	.modal-content {
		position: absolute;
		right: 0;
		bottom: 0;
		padding: rem(30);
	}

	.modal-price {
		color: $color-nevada;
		font-size: rem(30);
		font-family: $font-stack;
		letter-spacing: rem(2);
		text-align: right;
		margin-bottom: rem(20);
	}
</style>



