<template>
	<ul class="instagram-feed">
		<!-- <li v-for="(feed, index) in feeds">
			<InstagramFeedEntry :mediaId="feed" :token="token" />
		</li> -->
		<li v-for="(feed, index) in tempImages">
			<InstagramFeedEntry :mediaId="feed" :token="token" />
		</li>
	</ul>
</template>

<!--
	COMPONENT DESCRIPTION

	At the time of writing, the two flower-animations are set to autoplay in different ways, in order to test the most performant approach..

-->

<script>
import {MapState } from 'vuex';
import { Power4, TimelineMax } from 'gsap';

import InstagramFeedEntry from './InstagramFeedEntry';

export default {
	name: 'InstagramFeed',
	components: {
		InstagramFeedEntry
	},
	props: {
		feed: {
			type: Array,
			required: true,
		},
		token: {
			type: String,
			required: true,
		},
	},
	mounted(){
		let scrElement = document.createElement( 'script' );

		this.loadedImages = this.tempImages.length;

		window.instagramProcessResult = function () {
			this.feeds = this.feed;
		}.bind(this);

		scrElement.setAttribute( 'src', 'https://api.instagram.com/v1/users/self/media/recent?access_token=' + this.token + '&callback=instagramProcessResult' );
		document.body.appendChild( scrElement );
	},
	methods: {
		imagesLoaded(){
			this.loadedImages--;

			if (this.loadedImages === 0) {
				console.log('ER DU FRIDGG!!!!!!?????', this.$parent);
				this.$parent.instagramFeedLoaded();
			}
		}
	},
	data: () => ({
		feeds: [],
		loadedImages: 0,
		tempImages: [
			'/static/images/instagram/1.jpg',
			'/static/images/instagram/2.jpg',
			'/static/images/instagram/3.jpg',
			'/static/images/instagram/4.jpg',
			'/static/images/instagram/5.jpg',
			'/static/images/instagram/6.jpg',
			'/static/images/instagram/7.jpg',
			'/static/images/instagram/8.jpg'
		]
	}),
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../assets/global';

.instagram-feed {
	// width: 100%;

	// position: fixed;
	// top: 0;
	// left: 0;

	// z-index: 9999;

	// background-color: #fff;

	// li {
	// 	display: inline-block;
	// }
}

.instagram-feed {
	padding: 0;
	margin: 0;
	display: flex;
	flex-direction: column;

	li {
		display: block;

		&:nth-child(even) {
			align-self: flex-end;
		}
	}
}
</style>



