<!--
	/*
	Trigger description:

		Trigger points are set based on a fixed position of the scene, either top or bottom.
	*/
-->

<template>
	<div class="application-frame">
		<SiteHeader />

		<span>modal display {{ modal.display }}</span>
		<SiteHeader />
		<Modal v-if="modal.display"></Modal>
		<div id="garden" @click="position">
			<h1>{{ $t('general_title') }}</h1>
			<div class="video-wrapper main-video">
				<video muted="true" loop="true" playsinline="true" preload="auto" autoplay src="/static/video/butterfly-long.mp4" ref="main-video"></video>
				<div class="video-filter"></div>
			</div>
			<div class="bg bg--main"></div>

			<Intro ref="intro" v-on:introComplete="introComplete" />

			<template v-for="(scene, index) in scenes">
				<SceneHero
					v-if="scene.contentType === 'hero'"
					:options="scene"
					:className="scene.uniqueClass"
					:ref="`${ scene.contentId }`"
					/>

				<SceneContentButterfly
					v-if="scene.contentId === 'butterfly-content'"
					:options="scene"
					:className="scene.uniqueClass"
					:ref="`${ scene.contentId }`"
					/>

				<SceneContentLadybird
					v-if="scene.contentId === 'ladybird-content'"
					:options="scene"
					:className="scene.uniqueClass"
					:ref="`${ scene.contentId }`"
					/>

				<SceneContentClover
					v-if="scene.contentId === 'clover-content'"
					:options="scene"
					:className="scene.uniqueClass"
					:ref="`${ scene.contentId }`"
					/>

				<SceneContentBlossom
					v-if="scene.contentId === 'blossom-content'"
					:options="scene"
					:className="scene.uniqueClass"
					:ref="`${ scene.contentId }`"
					/>
			</template>
		</div>
	</div>
</template>

<script>
import {MapState } from 'vuex';
import * as constants from './constants.js';

import GSDevTools from 'GSDevTools';

import SiteHeader from './components/SiteHeader';
import InstagramFeed from './components/InstagramFeed';
// import { TimelineMax } from 'gsap';

import Modal from './components/modal';
import Intro from './components/scenes/Intro';
import SceneHero from './components/scenes/SceneHero';
import SceneContent from './components/scenes/SceneContent';
import SceneContentButterfly from './components/scenes/SceneContentButterfly';
import SceneContentLadybird from './components/scenes/SceneContentLadybird';
import SceneContentClover from './components/scenes/SceneContentClover';
import SceneContentBlossom from './components/scenes/SceneContentBlossom';
import * as Constants from './constants';

export default {
	name: 'Garden',
	components: {
		Modal,
		SiteHeader,
		Intro,
		SceneHero,
		SceneContent,
		InstagramFeed,
		SceneContentButterfly,
		SceneContentLadybird,
		SceneContentClover,
		SceneContentBlossom,
	},
	data(){
		return {
			// debug/dev vars
			showProgressIndicator: false,
			showTriggerPositions: false, // scroll/trigger position debug
			showIntro: this.$store.state.showIntro,

			masterHeight: 0, // getting passed to the state after first render/mounted
			scenes: this.$store.state.scenes,
			modal: this.$store.state.modal,

			instagramFeed: this.$store.state.instagram.feed,
			instagramToken: this.$store.state.instagram.token,
			hideSiteHeader: this.$store.state.hideSiteHeader,
		};
	},
	computed: {
		progress(){
			return this.$store.state.currentProgress;
		},
	},
	mounted(){
		this.$store.commit({
			type: "windowHeight",
			windowHeight: window.innerHeight
		});

		this.showProgressIndicator = ( this.$store.state.appLogLevel > 0 ? true : false );
		// this.showTriggerPositions = ( this.$store.state.appLogLevel > 0 ? true : false );
		this.showTriggerPositions = false;

		this.$nextTick(this.setupScenes);
		this.$nextTick(this.setupScroll);

		if(this.showIntro === false){ // for dev, if no intro, go directly to butterflyhero intro
			this.$nextTick(this.introComplete);
		}
		this.$store.dispatch('prepareProucts');

		// Add GSAP debug for ability to "scroll" in TimelineMax progress
		// GSDevTools.create();
	},

	methods: {
		introComplete(){
			document.body.style.height = this.masterHeight + 'px';
			// document.body.style.maxHeight = 'none';
			document.body.style.overflow = 'auto';

			// when intro's intro is done, play butterflyhero's intro
			console.log('this.$refs.butterflyHero[0]: ', this.$refs.butterflyHero[0]);
			this.$refs.butterflyHero[0].playIntro();
		},
		setupScenes(){
			// document.body.style.maxHeight = '100vh';
			// document.body.style.overflow = 'hidden';
			let masterHeight = 0;
			// arrow function syntax (() => {}) binds this to the parent context
			this.scenes.forEach(function(scene){
				scene.sceneTopOffset = masterHeight;
				scene.sceneHeight = this.$store.state.windowHeight * (scene.sceneHeight / 100);

				// setup trigger positions based on scene position in master timeline flow
				const defaultTriggerOffset = scene.sceneTopOffset;

				for (const trigger in scene.triggers) {
					// loop scene triggers
					if (scene.triggers.hasOwnProperty(trigger)) {
						const triggerOffsetValue = scene.triggers[trigger].value;
						const offsetFrom = scene.triggers[trigger].offsetFrom;

						if (typeof triggerOffsetValue == "number") {
							// if boolean aka. false, trigger-property not in use
							if (offsetFrom === "top") {

								if (triggerOffsetValue === 0) {
									scene.triggers[trigger].value = Math.floor(
										defaultTriggerOffset
									);
								} else {
									const vhPixel =
										defaultTriggerOffset +
										this.$store.state.windowHeight * (triggerOffsetValue / 100);
										// var convertToPositive = triggerOffsetValue * 1;
									scene.triggers[trigger].value = Math.floor(vhPixel);

								}
							} else if (offsetFrom === "bottom") {
								const bottomValue = defaultTriggerOffset + scene.sceneHeight;
								// console.log('bottomValue: ', bottomValue);
								if (triggerOffsetValue === 0) {
								scene.triggers[trigger].value = bottomValue;
								} else {
									var vhPixel =
										bottomValue +
										this.$store.state.windowHeight * (triggerOffsetValue / 100);
									scene.triggers[trigger].value = Math.floor(vhPixel);
								}
							}

							// console.log("reverse trigger? ", trigger.toLowerCase().indexOf("reverse"));
							if (trigger.toLowerCase().indexOf("reverse") < 0) {
								// if trigger is not a reverse trigger, put the value to test for testing min and max height scene
								scene.triggerValues.push(scene.triggers[trigger].value);
							}

							// show scene triggers in the DOM
							if ( (this.showTriggerPositions && trigger == "prepare") || trigger == "intro" || trigger == "outro" || trigger == "prepareReverse") {
								var triggerElem = document.createElement("div");
								triggerElem.innerHTML = scene.name + ": " + trigger;
								triggerElem.style.top = scene.triggers[trigger].value + "px";
								triggerElem.classList.add("painted-trigger");
								triggerElem.classList.add("trigger-" + trigger);
								document.body.append(triggerElem);
							}
						}
					}
				}
				const introOffset = (scene.triggers.intro.value ? scene.triggers.intro.value : 0) - masterHeight;
				masterHeight += scene.sceneHeight + introOffset;

			}.bind(this));

		  	this.$store.commit({
				type: "masterHeight",
				masterHeight
		  	});
		 	// document.body.style.height = masterHeight + "px";

		  	this.$store.commit("appLoaded", true, this.$i18n.locale);
		},
		position(event) {
			this.$store.state.position = {
				x: event.clientX,
				y: event.clientY,
			};
		},
		setupScroll() {
			// setting progression
			let lastKnownScrollPosition = 0;
			let oldScrollPosition = 0;
			let scrollTicking = false;

			window.addEventListener("scroll", e => {
				lastKnownScrollPosition = window.scrollY;

				if (!scrollTicking) {
					window.requestAnimationFrame(() => {
						oldScrollPosition = this.scrollHandler(
							oldScrollPosition,
							lastKnownScrollPosition
						);

						scrollTicking = false;
					});

					scrollTicking = true;
				}
			}, false);
		},

		scrollHandler(oldScrollPosition, scrollPos) {
			let progressPercent = (scrollPos / (this.$store.state.masterHeight - this.$store.state.windowHeight)) * 100;
			progressPercent = progressPercent.toFixed(4);

		  	const pixelProgress = scrollPos;

		  	const direction = scrollPos > oldScrollPosition ? constants.FORWARD : constants.BACKWARD;
		  	this.$store.commit("currentProgress", {
				percent: progressPercent,
				pixels: Math.floor(pixelProgress),
				direction
			});
			this.setActiveSceneState(scrollPos, direction);
			return scrollPos;
		},

		setActiveSceneState(scrollPos, direction) {
			// console.log("scrollPos: ", scrollPos);
			this.scenes.forEach((scene, sceneIterator) => {
				// intro state active
				if (
					scene.triggers.intro.value &&
					scrollPos >= scene.triggers.intro.value &&
					scrollPos <= (scene.sceneHeight + scene.triggers.intro.value)) {
					// console.log('intro: ', scene.name);

					this.$store.commit({
						type: 'sceneState',
						sceneIterator,
						state: constants.ONGOING,
					});
				} else {
					this.$store.commit({
						type: 'sceneState',
						sceneIterator,
						state: constants.OUT_OF_VIEW,
					});
				}

				// Prepare trigger
				if (
					scene.triggers.prepare.value != false &&
					scene.triggers.prepareReverse.value != false &&
					scrollPos >= scene.triggers.prepare.value &&
					scrollPos <= scene.triggers.prepareReverse.value &&
					!scene.isPrepared
					){

					this.$store.commit({
						type: 'prepareState',
						sceneIterator,
						state: constants.PREPARE
					});
				}

				// reset trigger
				if (
					scene.isPrepared &&
					((scene.triggers.prepare.value != false &&
					scrollPos <= scene.triggers.prepare.value) ||
					(scene.triggers.prepareReverse.value != false &&
					scrollPos >= scene.triggers.prepareReverse.value))
				){
					this.$store.commit({
						type: 'prepareReset',
						sceneIterator,
					});
				}
			});
		},
	},
};
</script>

<style lang="scss">
@import './assets/global';

#garden {
	width: 100%;
	min-height: 100vh;
}

.video-wrapper {
	width: 100%;
	height: 100vh;
	// margin-bottom: 50vh;
	z-index: 1;
	overflow: hidden;
	pointer-events: none;
	user-select: none;

	video {
		position: fixed;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: 50% 50%;
	}
	.video-filter {
		position: fixed;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;

		background-color: rgba(50, 50, 50, 0.3);
	}
}

.progress-indicator { // tmp
	position: fixed;
	top: 0;
	right: 0;
	padding: rem(10) rem(12);
	z-index: 5; // tmp

	&:hover {
		background-color: red;
	}
}

.painted-trigger {
	position: absolute;
	top: 0; // gets overwritten
	right: 0;
	border-bottom: rem(1) solid green;
	z-index: 6;

	&.trigger-prepare {
		right: rem(100);
		color: red;
	}
}
</style>



