<template>
	<div :id="id" class="flower" :class="[position]">
		<lottie :id="id" :options="this.defaultOptions" v-on:animCreated="handleAnimation" v-on:animCompleted="animationCompleted" />

		<!--
		width and height not set on element, so responsive size can be acheived with css
		:height="parseInt(height)" :width="parseInt(width)"
		-->
	</div>
</template>

<script>

import LottieBase from './LottieBase';
// import oneLeafAnimationData from '../../../static/animation-json/one-leaf.json';
import oneLeafAnimationData from '../../../static/animation-json/oneleaf-mask.json';
// import oneLeafAnimationData from '../../../static/animation-json/test/OneLeaf_NewBodyMovin.json';

export default {
	name: 'FlowerOneLeaf',
	components: {
		lottie: LottieBase, // <lottie :options="defaultOptions" :height="400" :width="400" v-on:animCreated="handleAnimation"/>
	},
	props: {
		id: {
			type: String,
			default: null,
		},
		position: {
			type: String,
			default: 'top-right',
		},
		autoplay: Boolean,
		// width: {
		// 	type: Number,
		// 	default: 300,
		// },
		// height: {
		// 	type: Number,
		// 	default: 300,
		// },
		width: String,
		height: String,
	},
	data(){
		return {
			defaultOptions: {
				// width: 300,
				// height: 300,
				animationData: oneLeafAnimationData,
				loop: false,
				autoplay: false,
			},
			animationSpeed: 1,
			direction: 1, // forward animation,
			totalFrames: 0,
		};
	},
	// beforeMount(){
	// 	console.log('this.position: ', this.position);
	// 	this.position = 'flower--' + this.position;
	// },
	mounted(){
		/* eslint-disable no-alert, no-console */
		// console.log('flower mounted.. Id: ', this.$props.id);
		/* eslint-enable no-alert, no-console */

		if(this.$props.autoplay === true){
			this.anim.play();
		}
		// if(window.bodymovin){
		//   console.log('lottie ready');

		//   this.anim.setSpeed(1);
		//   this.anim.play();
		// }
	},
	methods: {
		handleAnimation(anim){ // callback of lottie animCreated
			this.anim = anim;
			this.anim.setDirection(this.direction); // forward animation to start with(might not really be necessary)
			this.totalFrames = this.anim.getDuration(true); // If true, returns duration in frames, if false, in seconds.
			// console.log('flower duration: ', this.totalFrames);
		},
		stop(){
			this.anim.stop();
		},
		play(){
			this.anim.play();
		},
		pause(){
			this.anim.pause();
			// this.$state.commit('paused', true);
		},
		setReverse(){
			this.anim.setDirection(-1); // Reverse animation
		},
		setForward(){
			this.anim.setDirection(1); // Forward animation
		},
		changeSpeed(speed){
			this.anim.setSpeed(speed ? speed : this.animationSpeed);
		},
		animationCompleted(){
			// console.log('complete from flower?', this.$el);
			this.$emit('animationCompleted', this.anim);
		},
		jumpToFrame(frame){
			if(frame < 0){
				frame = 0
			} else if(frame > this.totalFrames){
				frame = this.totalFrames;
			}
			// console.log('jumping? - frame: ', frame);
			this.anim.goToAndStop(frame, true); // "true" for frame input
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/global';

.flower {
	// width: 40vw;
	// height: 40vw;
	
	// canvas {
	// 	width: 100%;
	// 	height: 100%;
	// }
}
.flower--top-right,
.flower--top-left,
.flower--bottom-left,
.flower--bottom-right {
	position: absolute;
	z-index: 3;
}
.flower--top-left {
	top: 0;
	left: 0;
	// transform: rotate(90deg);
}
.flower--top-right {
	top: 0;
	right: 0;
	transform: rotate(90deg);
}
.flower--bottom-left {
	bottom: 0;
	left: 0;

	transform: rotate(-90deg);
}
.flower--bottom-right {
	bottom: 0;
	right: 0;

	transform: scaleX(-1) rotate(-90deg);
}
</style>



