<template>
	<div class="intro">
		<div class="bg bg--intro" ref="bg"></div>

		<SceneHeadline
			ref="headline"
			headline="Intro headline"
			manchet="Her har vi en manchet-tekst.. $t(data.intro.headline)"
			/>

		<MqShow if="md" ref="FlowerOneLeaf1mq">
			<FlowerOneLeaf
				ref="FlowerOneLeaf1"
				id="intro-flower1"
				position="flower--top-right"
				width=400
				height=500
				/>
		</MqShow>
		<!-- Only FlowerOneLeaf2 has the animComplete, as it's a bit delayed and the last one to complete -->
		<FlowerOneLeaf
			ref="FlowerOneLeaf2"
			id="intro-flower2"
			position="flower--bottom-left"
			width=400
			height=500
			v-on:animationCompleted="flowerAnimCompleted"
			/>

	</div>
</template>

<!--
	COMPONENT DESCRIPTION

	At the time of writing, the two flower-animations are set to autoplay in different ways, in order to test the most performant approach..

-->

<script>
import {MapState } from 'vuex';
// import ProgressMixin from '../mixins/ProgressMixin';

import SceneHeadline from '../SceneHeadline';
import FlowerOneLeaf from '../animations/FlowerOneLeaf';

export default {
	name: 'Intro',
	// mixins: [ProgressMixin],
	components: {
		SceneHeadline,
		FlowerOneLeaf,
	},
	props: {
		options: null, // scene object from store state scenes
	},
	data(){
		return {
			introAnimationRan: false,
			flowerAnimCount: 0,
		};
	},
	created(){
		this.tl = new TimelineMax({ paused: false }); // headline intro animation (overwrite mixin default)
		this.outroTl = new TimelineMax({ paused: true, autoRemoveChildren: false });
	},
	mounted(){
		if(this.$store.state.showIntro){ // test if intro should be shown (disable in dev mode)
			this.$nextTick(this.setupScene);
		} else {
			this.$el.classList.add('hidden');
			this.$el.classList.remove('active-pointer');
			this.$el.classList.add('inactive-pointer');
		}
	},

	methods: {
		flowerAnimCompleted(){
			this.flowerAnimCount++;
			this.introAnimationRan = true;
			console.log('intro anim complete - this.introAnimationRan: ', this.introAnimationRan);
			

			// console.log('setting up  -  - - - - - -emitting introComplete');

			if(this.flowerAnimCount == 1){ // first roundtrip is flower animation IN done
				setTimeout(()=>{
					this.runOutro();
					this.$emit('introComplete');
					console.log('emitting intros intro complete');
					this.$store.commit({
						type: 'showMenu',
						hide: false
					});
				// }, 3000);
				}, this.$store.state.introStallDuration);
			} else if(this.flowerAnimCount == 2){ // second roundtrip is flower animation OUT done
				// this.$emit('introComplete');

				// Now we know the intros outro is done, then hide it
				this.$el.classList.add('hidden');
			}
		},
		runOutro(){
			this.outroTl.delay(0).play();
			if(this.$refs.FlowerOneLeaf1mq.$mqAliases.md){
				this.$refs.FlowerOneLeaf1.setReverse();
				this.$refs.FlowerOneLeaf1.play();
			}
			this.$refs.FlowerOneLeaf2.setReverse();
			this.$refs.FlowerOneLeaf2.play();
		},
		setupScene(){
			var bg = this.$refs.bg;

			this.outroTl
				.fromTo( // animate headline up and opacity zero
					this.$refs.headline.$el,
					2,
					{
						y: '-=0px',
						autoAlpha: 1,
						// ease: Linear.easeNone,
						ease: Power4.easeInOut,
					},
					{
						y: '-=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
						autoAlpha: 0,
						// ease: Linear.easeNone,
						ease: Power4.easeInOut,
					}
				)
				.progress(1) // caching animation
				.progress(0) // set stort pos back to 0
				// .pause()
				;

			// this.outroTl.progress(1).progress(0); // gsap "caching" for "stop motion animations"-ish

			if(this.$store.state.currentProgress.pixels === 0 && !this.introAnimationRan){ // initial page load
				this.animateFlowers();
				this.headlineFadeIn();
			} else {
				// position flowers and headline outro animation based on progress
				// this.outroTl
				// 	.to( // animate headline up and opacity zero
				// 		this.$refs.headline.$el,
				// 		0,
				// 		{
				// 			// y: '-=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
				// 			opacity: 0,
				// 			ease: Linear.easeNone,
				// 		}
				// 	);
			}
		},

		headlineFadeIn(){ // onload/after preload
			// this.tl.from(this.$refs.headline.$el, 1.5, { y: '40px', opacity: 0, ease: Power4.easeOut });
			this.tl
				.fromTo(this.$refs.headline.$el, 3,
					{
						// y: '+=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
						scale: 0.8,
						autoAlpha: 0,
						ease: Power2.easeOut
					},
					{
						// y: '+=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
						scale: 1,
						autoAlpha: 1,
						ease: Power2.easeOut
					}
				);
		},
		animateFlowers(){
			if(this.$refs.FlowerOneLeaf1mq.$mqAliases.md){
				this.$refs.FlowerOneLeaf1.play();
			}
			this.delayedSecondFlower();
		},
		delayedSecondFlower(){
			setTimeout(() => { // slight delay on second flower animation
				this.$refs.FlowerOneLeaf2.play();
			}, 350);
		},
		progressFlowers(progress){ // can be used for butterfly's and all the other animations as well
			if(this.$refs.FlowerOneLeaf1mq.$mqAliases.md){
				var progressFlower1 = this.calcAnimationFrame(this.$refs.FlowerOneLeaf1, progress);
				this.$refs.FlowerOneLeaf1.jumpToFrame(progressFlower1);
			}
			var progressFlower2 = this.calcAnimationFrame(this.$refs.FlowerOneLeaf2, progress);
			this.$refs.FlowerOneLeaf2.jumpToFrame(progressFlower2);
			// var progressFlower2 = this.$refs.FlowerOneLeaf2.totalFrames - (this.$refs.FlowerOneLeaf2.totalFrames / 100) * (progress * 100);

			// console.log('------------------------------------------- progress flowers?: ', progress);
			// console.log('------------------------------------------- progress flowers?: ', this.calculateSceneProgress());
			// console.log('progress flowers?: ', this.$refs.FlowerOneLeaf2.totalFrames);

		},

		// runIntro(progress){
		// 	// this.progressFlowers(progress);
		// 	// console.log('this.outroTl: ', this.outroTl);
		// 	// this.outroTl.tweenTo(progress);
		// 	if(this.introTl){
		// 		this.introTl.progress(progress);
		// 	}
		// },

		// runOutro(progress){
		// 	this.progressFlowers(progress);
		// 	// console.log('this.outroTl: ', this.outroTl);
		// 	// this.outroTl.tweenTo(progress);
		// 	this.outroTl.progress(progress);
		// },

		// replay(){ // SAVE: Nice to have around for other animations
		// 	// this.headlineFadeOut();
		// 	this.$refs.FlowerOneLeaf1.setForward();
		// 	this.$refs.FlowerOneLeaf1.changeSpeed(1);
		// 	this.$refs.FlowerOneLeaf1.play();

		// 	this.$refs.FlowerOneLeaf2.setForward();
		// 	this.$refs.FlowerOneLeaf2.changeSpeed(1);
		// 	this.$refs.FlowerOneLeaf2.play();
		// },
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import '../../assets/global';

.intro {
	position: fixed;
	top: 0;
	left: 0;
	color: #fff;
	width: 100%;
	height: 100vh;
	// background-color: pink;
	// z-index: 1;

	h1 {
		color: grey;
	}
	.manchet {
		color: grey;
	}

	.scene-headline {
		z-index: 3;
	}

	#intro-flower1,
	#intro-flower2 {
		width: rem(120);

		@include mq($from: screen-xs){
			width: rem(200);
		}
		@include mq($from: screen-sm){
			width: rem(250);
		}
		@include mq($from: screen-md){
			width: rem(350);
		}
		@include mq($from: screen-lg){
			width: rem(450);
		}
		@include mq($from: screen-xl){
			width: rem(650);
		}
	}
}
</style>



