// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import VueI18n from "vue-i18n";
import FineMq from "fine-mq";
import App from "./App";

import store from "./store/store";
import { languages, defaultLocale } from './locale';
Vue.config.productionTip = false;

// Vue.use(VueMq, { // mapped the same as the css mq
//   breakpoints: {
// xxs: 450,
// xs: 567,
// sm: 768,
// smplus: 950,
// md: 1024,
// lg: 1150,
// xl: 1280,
// xxl: 1440,
// xxxl: Infinity,
//   }
// })

const messages = Object.assign(languages);
const defaultLoc = document.querySelector('html').getAttribute('lang') ? document.querySelector('html').getAttribute('lang').replace('-', '_') : defaultLocale;
// Create VueI18n instance with options
Vue.use(VueI18n);
const i18n = new VueI18n({
	locale: defaultLoc,
	messages, // set locale messages
	fallbackLocale: 'en',
});
Vue.use(FineMq, {
	aliases: {
		// sm: 680, // <=> [0, 680]
		// md: [681, 1024],
		// lg: [1025], // <=> [1025, Infinity]
		// landscape: '(orientation: landscape)',
		// an_alias_name: {
		//   screen: true,
		//   minWidth: '23em',
		//   maxWidth: '768px'
		// }
		xxs: [450],
		xs: [567],
		sm: [768],
		smplus: [950],
		md: [1024],
		lg: [1150],
		xl: [1280],
		xxl: [1440],
		xxxl: [1680],
	},
});

/* eslint-disable no-new */
new Vue({
	el: '#pandora-garden',
	i18n,
	store,
	components: { App },
	template: '<App/>',
});



