import { Power1, Power2, Power4, TimelineMax } from 'gsap';
import { mapState } from 'vuex';
import CustomEase from 'GSCustomEase';
import {
	PREPARE,
	PREPARE_DONE,
	ONGOING, OUT_OF_VIEW, } from '../../constants';

var ProgressMixin;

export default ProgressMixin = {
	created(){
		this.tl = new TimelineMax({ paused: true });

		this.introTl = new TimelineMax({ paused: true });
		this.outroTl = new TimelineMax({ paused: true });
		// console.log('hej fra mixin created - scene: ', this.options.name);
	},
	data() {
		return {
			scenePrepared: false,
			scenePrepareRan: false,
			sceneReversePrepared: false,
			scenePrepareReverseRan: false,
		};
	},
	mounted(){
		this.$store.subscribe(this.handleProgress.bind(this));
	},
	computed: mapState({
		inView(state) {
			return state.scenes.filter(store => store.name === this.options.name)[0].state === ONGOING;
		},
	}),

	methods: {
		getProgressValue(){
			const progress = this.$store.state.currentProgress;
			const triggerValue = this.options.triggers.intro.value;
			let percent = 0;

			if(
				progress.pixels >= triggerValue &&
				progress.pixels <= (triggerValue + this.options.sceneHeight)
			){
				const pos = progress.pixels - triggerValue;
				percent = 100 / (this.options.sceneHeight / pos);
			} else if(progress.pixels < triggerValue) {
				percent = 0;
			} else if(progress.pixels > (triggerValue + this.options.sceneHeight)) {
				percent = 100;
			}
			return percent / 100;
		},
		handleProgress(){
			// console.log(`scene: ${this.options.name}`, this.$store.state.scenes.filter(scene => scene.name === this.options.name)[0].state);
			const sceneOptions = this.$store.state.scenes.filter(scene => scene.name === this.options.name)[0];
			const progress = this.$store.state.currentProgress;
			const sceneMin = Math.min(...this.options.triggerValues);
			const sceneMax = Math.max(...this.options.triggerValues);

			if (this.introTl && sceneOptions.state === ONGOING){
				this.runIntro(this.getProgressValue());
				
			}
			
			// if (this.runOutro && this.outroTl && sceneOptions.state === ONGOING){ // butterhero, as the first, nevers gets ONGOING
			if (this.runOutro && this.outroTl){
				this.runOutro(this.getProgressValue());
			}
			if (this.runPrepare && sceneOptions.triggerState === PREPARE) {
				this.runIntro(this.getProgressValue()); // will clean errors
				if (this.runPrepare) {
					this.runPrepare();
				}
				this.$store.commit({
					type: 'prepareDone',
					sceneName: this.options.name,
				});
			}
			
			if(this.$el){ // $el is not set when mixin is created/mounted
				// TODO - FIGURE OUT IF THIS SHOULD BE DONE ON THE SCENECONTENTS THEM SELVES
				//const inactive = progress.pixels >= sceneMin && progress.pixels <= sceneMax;
				// this.$el.classList.toggle('active-pointer', sceneOptions.state === ONGOING);
				// this.$el.classList.toggle('inactive-pointer', sceneOptions.state === OUT_OF_VIEW);
			}
		},
		calcAnimationFrame(el, progress){ // used for scroll based timeline
			var calc = el.totalFrames - ((el.totalFrames / 100) * (progress * 100));
			return calc;
		},

		runIntro(progress){
			if(this.introTl){
				this.introTl.progress(progress);
			}
		},
		runOutro(progress){
			if(this.outroTl){
				this.outroTl.progress(progress);
			}
		},
	},
};



