import { PREPARE_DONE } from '../constants';

export default {
    appLoaded(state, loaded, currentLocale = ''){
        state.appLoaded = loaded;
    },
    masterHeight(state, payload){
        state.masterHeight = payload.masterHeight;
    },
    windowHeight(state, payload) {
        state.windowHeight = payload.windowHeight;
    },
    currentProgress(state, payload){
        // payload example:
        // {
        // 	percent: 0,
        // 	pixels: 0,
        // 	direction: 'forward/backward'
        // }
        state.currentProgress = payload;
    },
    sceneState(state, payload){
        state.scenes[payload.sceneIterator].state = payload.state;
    },
    prepareState(state, payload){
        state.scenes[payload.sceneIterator].triggerState = payload.state;
    },
    prepareDone(state, payload){
        const i = state.scenes.findIndex(scene => scene.name === payload.sceneName);
        state.scenes[i].triggerState = PREPARE_DONE;
        state.scenes[i].isPrepared = true;
    },
    prepareReset(state, payload){
        //const i = state.scenes.findIndex(scene => scene.name === payload.name);
        state.scenes[payload.sceneIterator].isPrepared = false;
        state.scenes[payload.sceneIterator].triggerState = '';
    },
    updateProducts(state, products){
        state.products = products;
        
    },
    showMenu(state, payload){
        state.hideSiteHeader = payload.hide;
        console.log(state.hideSiteHeader);
    },
	openModal(state, payload) {
		const modalContent = state.products.filter(product => product.sku === payload.sku)[0];
		state.modal.display = true;
		state.modal.isCard = payload.isCard;
		state.modal.content = modalContent;
	},
	closeModal(state) {
		state.modal.display = false;
		state.modal.content = {};
	},
};



