<template>
	<header :class="[isHidden ? 'site-header is-hidden' : 'site-header']">
		<div class="site-header__inner">
			<h1 class="site-header__logo"><a href="#"><img src="/static/images/logo.svg"></a></h1>

			<nav :class="[showLanguageNav ? 'site-header__language-nav is-active' : 'site-header__language-nav']">
				<button type="button" v-on:click="showLanguageNav = !showLanguageNav; showProducts = false"><span class="site-header__language-nav__flag"><img :src="selectedCountryIcon" :alt="selectedCountry"></span></button>

				<div :class="[showLanguageNav ? 'slide-in-modal slide-in-modal--auto-width slide-in-modal--from-left is-active' : 'slide-in-modal slide-in-modal--auto-width slide-in-modal--from-left']">
					<div class="slide-in-modal__inner">
						<ul>
							<li v-for="country in countries" :key="country.countryCode">
									<a href="" v-on:click="onCountryChange(country.countryCode, $event)"><span class="site-header__language-nav__flag"><img :src="country.icon" :alt="country.countryCode"></span>{{ country.name }}</a>
							</li>
						</ul>
					</div>
				</div>
			</nav>

			<nav :class="[showProducts ? 'site-header__products is-active' : 'site-header__products']">
				<button type="button" class="site-header__products__trigger" v-on:click="showProducts = !showProducts; showLanguageNav = false">
					<span class="site-header__products__trigger-text">See the jewellery</span>
					
					<span class="site-header__products__trigger-dots">
						<span></span><span></span><span></span>
					</span>
				</button>
				
				<div :class="[showProducts ? 'slide-in-modal is-active' : 'slide-in-modal']">
					<div class="slide-in-modal__inner">
						<h2 class="slide-in-modal__headline">Pandora Garden</h2>
						<p class="slide-in-modal__text">A selection from the new Spring Collection</p>

						<ul class="products">
							<li v-for="product in products" :key="product.sku">
								<button type="button" v-on:click="openModal(product.sku)">
									<img :src="product.images.packshot">
								</button>
							</li>
						</ul>

						<a href="#" class="products-link">Shop the full collection</a>
					</div>
				</div>
			</nav>
		</div>
		<div class="site-borders">
			<span class="site-borders__element site-borders__element--left"></span>
			<span class="site-borders__element site-borders__element--bottom"></span>
			<span class="site-borders__element site-borders__element--right"></span>
		</div>
	</header>
</template>

<!--
	COMPONENT DESCRIPTION

	At the time of writing, the two flower-animations are set to autoplay in different ways, in order to test the most performant approach..

-->

<script>
import {MapState } from 'vuex';

// import { TweenMax, TimelineMax } from 'gsap';
import { Power4, TimelineMax } from 'gsap';

import InstagramFeedEntry from './InstagramFeedEntry';
import Utils from './mixins/Utils';

export default {
	name: 'SiteHeader',
	components: {
		InstagramFeedEntry
	},
	created(){
	},
	mounted() {
		
	},
	// computed: {
	// 	progress(){
	// 	}
	// },
	methods: {
		openModal(sku) {
			console.log('sku: ', this)
		},
		onCountryChange(countryCode, event) {
			event.preventDefault();
			alert('transfering to: ', Utils.methods.getCountryLink(countryCode, this.$store.state))
			// TODO add tracking at this point
			//window.location = Utils.methods.getCountryLink(countryCode);
		}
	},
	data() {
		return {
			showProducts: false,
			showLanguageNav: false,
			products: this.$store.state.products,
			countries: this.$store.state.countries,
			selectedCountryIcon: this.$store.state.countries.filter((c) => c.countryCode === this.$i18n.locale)[0].icon,
			selectedCountry: this.$i18n.locale,
		}
	},
	computed: {
		isHidden() {
			return this.$store.state.hideSiteHeader;
		},
	}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../assets/global';

$hideSiteHeaderTransition: transform 0.4s ease;

.site-header {
	position: relative;
	z-index: 9999;

	&__inner {
		display: block;
		width: 100%;
		height: rem($main-menu-height);
		padding: 0;

		background-color: $color-white;

		position: fixed;
		top: 0;
		left: 0;
		z-index: 1;

		transition: $hideSiteHeaderTransition;

		@include mq($from: screen-md) {
			height: rem($main-menu-height-md);
		}
	}

	button[type="button"] {
		cursor: pointer;
	}

	&__logo {
		display: block;
		width: rem(90);
		padding: 0;
		margin:  rem(11) 0 0;
		
		position: absolute;
		left: 50%;
		top: 0;
		transform: translateX(-50%);

		@include mq($from: screen-md) {
			width: rem(142);
			margin:  rem(23) 0 0;
		}

		img {
			display: block;
			width: 100%;
		}
	}

	&__language-nav {
		ul {
			display: block;
			padding: 0;
			margin: 0;
		}

		a {
			display: block;
			padding: rem(14) rem(70) rem(14) rem(16);

			text-decoration: none;
			color: $color-text;
			font-size: rem($menu-link-min);
			white-space: nowrap;

			transition: background-color .25s ease;

			@include mq($from: screen-sm) {
				font-size: rem($menu-link-mid);
			}

			@include mq($from: screen-md) {
				padding: rem(10) rem(70) rem(10) rem(29);

				// font-size: rem($menu-link-max);
			}

			@include mq($from: screen-lg) {
				padding: rem(14) rem(70) rem(14) rem(29);

				font-size: rem($menu-link-max);
			}

			&:hover {
				background-color: #EBECEC;
			}

			&.is-active {
				font-family: $font-stack-bold;
			}
		}

		button {
			width: rem(46);
			height: rem(40);
			padding: 0;
			margin: 0;
			appearance: none;

			outline: none;

			border: none;

			background-color: transparent;

			position: relative;

			transition: background-color .25s ease;

			@include mq($from: screen-md) {
				width: rem(86);
				height: rem(80);
			}

			.site-header__language-nav__flag {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}

		&__flag {
			display: inline-block;
			width: rem(15);
			height: rem(15);
			margin: 0 rem(15) 0 0;

			transform: translateY(rem(1));

			border-radius: 50%;

			background-color: rgba(0,0,0,.3);
			overflow: hidden;

			@include mq($from: screen-md) {
				width: rem(28);
				height: rem(28);
				margin: 0 rem(30) 0 0;

				transform: translateY(rem(4));
			}
		}

		&.is-active {
			button {
				background-color: #F6F7F7;
			}
		}
	}

	&__products {
		position: absolute;
		top: 0;
		right: 0;

		.slide-in-modal {
			text-align: center;
		}

		&__trigger {
			display: block;

			padding: 0;
			margin: 0;
			appearance: none;

			border: none;
			outline: none;

			background-color: $color-white;
		}

		&__trigger-text {
			display: none;
			margin-right: rem(-13);

			font-size: rem(15);
			text-transform: uppercase;
			font-family: $font-stack;

			transform: translate(0, rem(-5));

			transition: opacity .25s ease .1s, transform .25s ease;

			@include mq($from: screen-sm) {
				display: inline-block;

				font-size: rem(12);

				transform: translate(0, rem(-3));

			}

			@include mq($from: screen-md) {
				font-size: rem(15);

				transform: translate(0, rem(-5));
			}
		}

		&__trigger-dots {
			display: inline-block;
			padding: rem(4) rem(17) rem(10);

			transition: background-color .25s ease;

			@include mq($from: screen-md) {
				padding: rem(27) rem(30);
			}

			span {
				display: inline-block;
				width: rem(9);

				text-align: right;

				@include mq($from: screen-md) {
					width: rem(14);
				}

				&:before,
				&:after {
					content: '';
					display: inline-block;
					margin-bottom: rem(-4);
					width: rem(6);
					height: rem(6);

					border-radius: 50%;

					background-color: $color-carousel-pink;

					transition: background-color .25s ease;

					@include mq($from: screen-md) {
						width: rem(9);
						height: rem(9);
						margin-bottom: rem(2);
					}
				}

				&:after {
					margin-bottom: 0;
				}
			}
		}

		&.is-active {
			.site-header__products__trigger-text {
				opacity: 0;

				cursor: default;
				pointer-events: none;
				transform: translate(100%, rem(-5));

				transition: opacity .25s ease, transform .3s ease .1s;
			}

			.site-header__products__trigger-dots {
				background-color: #F6F7F7;

				& span:before,
				& span:after {
					background-color: #686C6F;
				}
			}
		}

		.slide-in-modal__inner {
			padding: 0 rem(40);

			@include mq($from: screen-md) {
				padding: 0 rem(70);
			}
		}
	}

	&__modal-link {
		text-align: right;
	}

	&.is-hidden {
		.site-header__inner {
			transform: translateY(-100%);
		}

		.site-borders {
			&__element {
				&--left {
					transform: translateX(-100%);
				}

				&--right {
					transform: translateX(100%);
				}

				&--bottom {
					transform: translateY(100%);
				}
			}
		}

		.slide-in-modal {
			transform: translateX(100%);

			transition: transform .15s ease, box-shadow .15s ease;
		}

		.slide-in-modal--from-left {
			transform: translateX(-100%);
		}
	}
}

.slide-in-modal {
	width: 100%;
	max-width: rem(1250);
	height: calc(100vh - rem(40));
	padding: 0;
	box-sizing: border-box;

	background-color: #F6F7F7;

	position: fixed;
	top: rem(40);
	right: rem(0);
	z-index: -1;

	transform: translateX(100%);

	transition: transform .35s ease, box-shadow .25s ease;

	@include mq($from: screen-md) {
		height: calc(100vh - rem(80));
		
		top: rem(80);
	}

	@include mq($from: screen-lg) {
		width: 55%;
	}

	&:before {
		content: '';
		display: block;
		width: 120%;
		height: rem(40);

		background-color: $color-white;

		position: absolute;
		top: 0;
		right: 0;
		
		transform: translateY(-100%);

		@include mq($from: screen-md) {
			height: rem(80);
		}
	}

	&__inner {
		height: 100%;
		overflow-y: auto;
		overflow-x: hidden;

		&::-webkit-scrollbar {
			width: rem(4);
		}

		&::-webkit-scrollbar-track {
		}

		&::-webkit-scrollbar-thumb {
			background-color: darkgrey;
			outline: 1px solid slategrey;
		}
	}

	&__headline {
		display: block;
		margin: rem(80) 0 rem(15);
		padding: 0;

		font-size: rem(60);
		font-weight: normal;
		text-align: center;
		color: $color-text;
	}

	&__text {
		display: block;
		margin: 0 0 rem(40);
		padding: 0;

		font-size: rem(30);
		text-align: center;
		color: $color-text;
	}

	&--from-left {
		right: auto;
		left: 0;

		transform: translateX(-100%);

		&:before {
			right: auto;
			left: 0;
		}
	}

	&--auto-width {
		@include mq($from: screen-md) {
			width: auto;
		}
	}

	&.is-active {
		transform: translateX(0);

		box-shadow: rem(5) 0 rem(40) 0 rgba(0,0,0,.3);
	}
}

.products {
	display: flex;
	flex-flow: row wrap;
	padding: 0;
	margin: 0 0 rem(40);

	li {
		display: block;
		flex: 0 1 25%;
		width: 25%;
		margin-bottom: rem(10);

		list-style-type: none;

		button,
		img {
			display: block;
			width: 100%;
		}

		button {
			border: none;
			outline: none;
			background-color: transparent;
		}

		img {
			transform: scale(1);

			transition: transform .35s ease;
		}

		button:hover img {
			transform: scale(1.1);
		}
	}
}

.products-link {
	display: inline-block;
	padding: rem(30) rem(60);
	margin-bottom: rem(100);

	font-size: rem($manchet-max);
	text-transform: uppercase;
	color: #333;
	text-decoration: none;

	border: rem(1) solid #333;
}

.site-borders {
	width: 0;
	height: 0;

	&__element {
		display: block;

		background-color: $color-white;

		position: fixed;
		z-index: 0;

		transition: $hideSiteHeaderTransition;

		&--left {
			width: rem(15);
			height: 100vh;

			left: 0;
			top: 0;

			@include mq($from: screen-md) {
				width: rem(30);
			}
		}

		&--right {
			width: rem(15);
			height: 100vh;

			right: 0;
			top: 0;

			@include mq($from: screen-md) {
				width: rem(30);
			}
		}

		&--bottom {
			height: rem(15);
			width: 100vw;

			left: 0;
			bottom: 0;

			@include mq($from: screen-md) {
				height: rem(30);
			}
		}
	}
}
</style>



