<template>
	<div class="scene-content" :class="[className, inView ? 'active-pointer' : '']">

		<!-- <div class="feed-wrapper" ref="feed">
			<div class="feed">
				feed 1
			</div>
			<div class="feed">
				feed 2
			</div>
			<div class="feed">
				feed 3
			</div>
			<div class="feed">
				feed 4
			</div>
			<div class="feed">
				feed 5
			</div>
			<div class="feed">
				feed 6
			</div>
			<div class="feed">
				feed 7
			</div>
		</div> -->

		<div class="feed-wrapper" ref="feed">
			<InstagramFeed :feed="instagramFeed" :token="instagramToken" />
		</div>

		<div class="content" ref="content">
			<div class="ladybird-fixed">
				<img src="/static/images/ladybird/ladybird.png" alt="">
			</div>
			<div class="text">
				Ladybirds <br> symbolise <br>
				happiness <br>
				and following <br>
				your bliss

				<div class="skinny">#PandoraGarden</div>
			</div>
		</div>
	</div>
</template>

<script>
import {MapState } from 'vuex';
import ProgressMixin from '../mixins/ProgressMixin';

import SceneHeadline from '../SceneHeadline';
import InstagramFeed from '../InstagramFeed';

export default {
	name: 'SceneContentLadybird',
	mixins: [ProgressMixin],
	components: {
		SceneHeadline,
		InstagramFeed,
	},
	props: {
		options: Object,
		// contentId: {
		// 	type: String,
		// 	default: 'butterfly',
		// },
		className: String,
	},
	mounted(){
		// console.log('mounted - this.options.name: ', this.options.name);
		// this.$nextTick(this.setupScene);
	},
	data() {
	  return {
		instagramFeed: this.$store.state.instagram.feed,
		instagramToken: this.$store.state.instagram.token,
	  };
	},
	methods: {
		instagramFeedLoaded() {
			console.log('THIS IS SPARTAAAA!!!!!');
			this.$nextTick(this.setupScene);
		},
		setupScene(){
			var bg = document.querySelector('.bg--main'),
				content = this.$refs.content,
				feed = this.$refs.feed;

			var feedHeight = feed.clientHeight;

			console.log('AERGNKEWRLNGERWKLNGERWKGRNERRGNE', feedHeight);

			// this.introTl
			// 	.set(content, {autoAlpha: 0});

			this.introTl
				.set(bg, {autoAlpha: 0});

			this.introTl
				.add('start')
				.fromTo(bg, 1,
					{
						autoAlpha: 0,
						ease: Linear.easeNone,
					},
					{
						autoAlpha: 1,
						ease: Linear.easeNone,
					}
				)
				.add('content-start')
				.fromTo(content, 1,
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						y: this.$store.state.windowHeight,
						autoAlpha: 0,
						ease: Linear.easeNone,
						delay: -1,
					},
					{
						y: '50%',
						autoAlpha: 1,
						ease: Linear.easeNone,
						delay: -1,
					}
				)
				.fromTo(feed, .4,
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						y: this.$store.state.windowHeight,
						autoAlpha: 1,
						ease: Linear.easeNone,
						delay: -0.3,
					},
					{
						y: this.$store.state.windowHeight / 25,
						autoAlpha: 1,
						ease: Linear.easeNone,
						delay: -0.3,
					}
				)
				.fromTo(feed, 2,
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						y: this.$store.state.windowHeight / 25,
						autoAlpha: 1,
						ease: Linear.easeNone,
						// delay: -1,
					},
					{
						y: -feedHeight,
						autoAlpha: 1,
						ease: Linear.easeNone,
						// delay: -1,
					}
				)
				.add('content-outro')
				.fromTo(content, 1,
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						y: '50%',
						autoAlpha: 1,
						ease: Linear.easeNone,
						delay: -.4,
					},
					{
						y: '-=' + this.$store.state.windowHeight + 'px',
						autoAlpha: 0,
						ease: Linear.easeNone,
						delay: -.4,
					}
				)
				.add('content-end')
				.fromTo(bg, .6,
					{
						autoAlpha: 1,
						ease: Linear.easeNone,
						delay: -1,
					},
					{
						autoAlpha: 0,
						ease: Linear.easeNone,
						delay: -1,
					}
				)
				.add('end')
				.progress(1)
				.progress(0)
				// .pause()
				;
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/global';

.scene-content {
	opacity: 1;

	.scene-headline { // h2
		position: relative;
		font-size: rem(60);
		top: 4vw;
		left: 50%;
		transform: translateX(-50%);
		text-align: center;
	}

	.content {
		position: absolute;
		top: 0;
		right: rem(100);
		opacity: 0;
		visibility: hidden;
		width: 50vw;
		z-index: 10;

		.text {
			width: 60%;
			font-size: rem(80);
			font-weight: bold;
			z-index: 2;
		}
		.skinny {
			font-weight: normal;
		}

		.ladybird-fixed {
			position: relative;
			float: right;
			width: rem(300);
			height: rem(600);

			z-index: 1;
			overflow: hidden;

			img {
				position: absolute;
				top: 30%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 280%;
			}
		}
	}

	.feed-wrapper {
		position: absolute;
		top: 0;
		left: 10vw;
		opacity: 0;
		visibility: hidden;
		width: rem(720);
		padding-top: rem(50);
	}
}
</style>



