<!--
	Not in use since introducing only one video tag at top level
-->

<template>
	<div class="hero" :class="[type ? 'hero--video' : '']" v-if="type === 'video'">
		<!-- <video muted="true" :src="src" loop="true" playsinline="true" preload ref="video"></video> -->
	</div>
	<div class="hero" :class="[type ? 'hero--image' : '']" v-else-if="type === 'image'">
		<img :src="src" alt="Billede hero">
	</div>
</template>

<script>
export default {
	name: 'Hero',
	props: {
		type: {
			type: String,
			required: true,
			default: 'video',
		},
		src: {
			type: String,
			required: true,
			default: '/static/video/butterfly.mp4',
		},
	},
	// components: {},
	// mounted: {},
	data(){
		return {
			isInview: false,
		};
	},
	methods: {
		play(){
			this.$refs.video.play();
			// console.log('playing video');
		},
		pause(){
			this.$refs.video.pause();
			// console.log('playing video');
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../assets/global';

.hero {
	width: 100%;
	height: 100vh;
	// overflow: hidden;
}
</style>



