<template>
	<div class="scene-headline">
		<h1>{{ headline }}</h1>
		<div class="scene-headline__manchet" v-if="manchet">
			{{ manchet }}
		</div>
	</div>
</template>

<script>
export default {
	name: 'SceneHeadline',
	props: {
		headline: {
			type: String,
			required: true,
			default: 'Scene headline',
		},
		manchet: {
			type: [Boolean, String],
			default: false,
		},
	},
	// data(){
	// 	return {
	// 		// headline: 'Scene headline',
	// 	};
	// },
};
</script>

<style lang="scss" scoped>
@import '../assets/global';

.scene-headline {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 2;
	max-width: rem(900);

	&__manchet {
		margin-top: rem(12);
	}
}
h1 {
	font-size: rem(32);
	text-align: center;

	@include mq($from: screen-sm){
		font-size: rem(42);
	}
	@include mq($from: screen-md){
		font-size: rem(52);
	}
	@include mq($from: screen-lg){
		font-size: rem(60);
	}
}
</style>



